import { Routes, RouterModule } from '@angular/router';

import { HomeComponent } from './home/index';
import { LoginComponent } from './login/index';
import { RegisterComponent } from './register/index';
import { FreasherbasedComponent }from './freasherbased/freasherbased.component';
import { ExpriencebasedComponent } from './expriencebased/expriencebased.component';
import { FormDataComponent } from './form-data/form-data.component';



import { AuthGuard } from './_guards/index';

const appRoutes: Routes = [
    { path: 'home', component: HomeComponent, canActivate: [AuthGuard] },
    {path: 'form-data', component:FormDataComponent},
    { path: 'login', component: LoginComponent },
    { path: 'register', component: RegisterComponent },
    {path:'freasherbased',component:FreasherbasedComponent},
    {path:'expriencebased',component:ExpriencebasedComponent},


    // otherwise redirect to home
    { path: '**', redirectTo: 'home' }
];

export const routing = RouterModule.forRoot(appRoutes);



