import { Component, OnInit } from '@angular/core';
import { PostDataService } from './../post-data.service';

@Component({
  selector: 'app-form-data',
  templateUrl: './form-data.component.html',
  styleUrls: ['./form-data.component.css']
})
export class FormDataComponent implements OnInit {

  constructor(public myData:PostDataService ) { }
   fullname:string= this.myData.fullname;
   email:string = this.myData.email;
   mobilenum:number =this.myData.mobilenum;
   careerObject:string = this.myData.careeerObjective;
   degree:string=this.myData.degree;
   degCollege:string=this.myData.degCollege;
   branch:string=this.myData.branch;
   cgpa:number=this.myData.cgpa;
   bof:string=this.myData.bof;
   bofCollege:string=this.myData.bofCollege;
   stream:string=this.myData.stream;
   percent:number=this.myData.percent;
   secondryEdu:string=this.myData.secondryEdu;
   schoolName:string=this.myData.schoolName;
   field:string=this.myData.field;
   marks:number=this.myData.marks;
   technical:string=this.myData.technical
   certificates:string=this.myData.certificates;
   achivements:string=this.myData.achivements;
   YearofExp:number=this.myData.YearofExp;
   CompanyName:string=this.myData.CompanyName;
   SocialAct:string=this.myData.SocialAct;
   gender:string=this.myData.gender;
   DOB:Date=this.myData.DOB;
   Address:string=this.myData.Address;
   Declaration:string=this.myData.Declaration;
  ngOnInit() {
  }

}



